<template>
  <div class="address-wrap">
    <div class="top-prize"></div>
    <div class="main-cont-box wrap-shadow flex-column">
      <span class="prize-title">{{prizeInfo.ticketName}}</span>
      <ButtonM class="prize-code">{{prizeInfo.ticketCode}}</ButtonM>
      <div class="img-box wrap-shadow">
        <img v-if="prizeInfo.ticketImg" :src="img_baseUrl+prizeInfo.ticketImg" alt="">
      </div>
      <p class="desc-tip">注：请复制兑奖码进行兑换</p>
      <p class="desc-text">兑奖说明：中奖者在兑奖过程中发生的任何费用、支出以及奖品个人所得税由中奖者自行承担。本次活动奖品不可兑换现金或作价销售。</p>
    </div>
    <ButtonM class="btn-ack" @click="ikown">我知道了</ButtonM>
    <PrizePhoneNum></PrizePhoneNum>
  </div>
</template>

<script>
  import ButtonM from '../../components/ButtonM/ButtonM';
  import PrizePhoneNum from './conponents/PrizePhoneNum/PrizePhoneNum';
  import {baseUrl} from "../../utils/request";
  export default {
    name: "virtual-card",
    components: {ButtonM, PrizePhoneNum},
    data() {
      return {
        prizeInfo: {},
        img_baseUrl: baseUrl.imgURL
      }
    },
    methods: {
      init() {
        this.prizeInfo = this.$route.params
        if(!this.prizeInfo.type) {
          this.$router.replace('/my-prize')
          return
        }
      },
      ikown() {
        this.$router.push('/my-prize')
      }
    },
    created() {
      this.init()
    }
  }
</script>

